import React from "react"
import "../styles/header.css"
import { slide as Menu} from "react-burger-menu";
import { Link , graphql, useStaticQuery} from "gatsby"
import { useBreakpoint } from "gatsby-plugin-breakpoints"

export default function Header({data}) {
    const { site } = useStaticQuery(query);
    const breakpoints = useBreakpoint();

    return (
        <header>
            <Menu width={250}>
                <span> --- Menu --- </span>
                <Link className="menu-item" to="/">Home</Link>
                <Link className="menu-item" to="/today_predict_forcus"></Link>
                <Link className="menu-item" to="/today_predict"></Link>

                {/* <Link className="menu-item" to="/past_predict"></Link> */}

                <Link className="menu-item" to="/note">Note</Link>
                <Link className="menu-item" to="/about"></Link>
            </Menu>

            {breakpoints.mobile ? 
            <div>
              <p className="header-logo-mobile">{site.siteMetadata.siteSubTitle}</p> 
              <p className="header-title-mobile">{site.siteMetadata.siteName}</p>
              <span className="kakugen-mobile"></span>
            </div>
                : null}
            {breakpoints.pc ? 
            <div>
              <p className="header-logo-pc">{site.siteMetadata.siteSubTitle}</p> 
              <p className="header-title-pc">{site.siteMetadata.siteName}</p>
              <span className="kakugen-pc"></span>
            </div>
            : null}


        </header>
    )
}

export const query = graphql`
  query {
    site {
      siteMetadata {
        siteName
        siteSubTitle
        siteDescription
        siteUrl
        siteOgpImage
        siteAuthor
      }
    }
  }
`